.cards{

    /* height: 170px; */
    /* background-color: #008000; */
    width: 17%;
    border: 1px solid rgb(0, 0, 0);
    margin: 10px 50px;
    display: flex;
    flex-direction: column;
    font-size: small;
    border-radius: 15px;

}
.card-row{
    margin-top: 3px;
    margin-bottom: 3px;
    /* border: 1px solid rgb(0, 255, 72); */

}
.card-logo>img{
    width: 60px;
}
@media screen and (max-width: 650px) {
    .weather-first-block{
        width: 70%;


    }
    .weather-info{

        margin-right: 0px;
        margin-bottom: 10px;
        
        }
        .weather-logo{
           margin-top: 10px;
          margin-left: 0px;
        }
 
}

.bold-text{
    font-weight: 600;
    font-size: medium;
}
@media only screen and (min-width:500px) and  (max-width: 650px) {
    .cards{
        width: 30%;

    }
  }
  @media screen and (max-width: 500px) {
   .cards{
    width: 70%;

   }
 
}