.home-body{
    /* background-color: rgb(255, 100, 100); */
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}
.first-block{
    display: flex;
    width: 100%;
    /* background-color: beige; */
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 20px;
}

.weather-first-block{
    height: 300px;
    /* background-color: rgb(255, 243, 113); */
    width: 30%;
    border-radius: 30px;
    display: flex;
    flex-direction: column; 
    border: 1px solid black;
}
.weather-info{

margin-right: 40px;
justify-content: center;

}
.weather-logo{
   
  margin-left: 40px;
}
.weather-icon{
    margin-top: 30px;
}
.weather-icon> img{
    width: 160px;
}
.astro{
    margin: 20px 0;
    /* border: 2px solid pink; */
    display: flex;
    justify-content: space-around;

}
.astro-data{
    /* border: 1px solid red; */
}
.second-block{
    /* height: 160px; */
    /* background-color: red; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 20px;
}
.weather-header{
    font-size: larger;
    font-weight: 600;
}
.astro-row{
    background-color: rgb(232, 232, 232);
}
.second-block-title{
    height: 60px;
    /* border: 1px solid black; */
    margin-top: 25px;
    display: flex;
    align-items: center;
    font-size: 30px;
    font-weight: 600;
    padding-left: 30px;
}

@media screen and (max-width: 650px) {
    .weather-first-block{
        width: 70%;


    }
    .weather-info{

        margin-right: 0px;
        margin-bottom: 10px;
        
        }
        .weather-logo{
           margin-top: 10px;
          margin-left: 0px;
        }
 
}